// Âä®ÊÄÅÂ±ïÁ§∫ÁöÑÂéüÁêÜÔºöÊää css Ê†∑ÂºèËΩ¨ÂåñÊàêÂ≠óÁ¨¶‰∏≤ÔºåÁÑ∂ÂêéÁî® setInterval ÊääÂ≠óÁ¨¶‰∏≤‰∏Ä‰∏™Â≠óÁ¨¶‰∏Ä‰∏™Â≠óÁ¨¶ÁöÑÂú®È°µÈù¢Â±ïÁ§∫Âá∫Êù•
import string from './style.js'

const demo = document.querySelector('#demo')
const demo2 = document.querySelector('#demo2')

// Âú® demo ÈáåÂ±ïÁ§∫Âá∫ÊñáÊú¨ÂÜÖÂÆπÔºåÂêåÊó∂ÔºåÂú® demo2 ÈáåÂêåÊ≠•Âä†‰∏äÊ†∑Âºè
let n = 1
demo.innerText = string.substr(0, n)
demo2.innerHTML = string.substr(0, n)

// ÊØèÈöî‰∏ÄÊÆµÊó∂Èó¥ÔºåÊâßË°å setInterval ÈáåÈù¢ÁöÑÂáΩÊï∞ÔºåÂπ∂ËÆ∞ÂΩïÊØèÊ¨°ÊâßË°åÂÆåÂêéËøîÂõûÁöÑ id
// ÂΩì n Â§ß‰∫éÂ≠óÁ¨¶‰∏≤ÈïøÂ∫¶Êó∂ÊöÇÂÅú
let id = setInterval(() => {
  n += 1
  if (n > string.length) {
    window.clearInterval(id)
    return
  }
  demo.innerText = string.substr(0, n)
  demo2.innerHTML = string.substr(0, n)
  demo.scrollTop = demo.scrollHeight // ÊªöÂä®Êù°Ê†πÊçÆÈ°µÈù¢Âá∫Áé∞ÁöÑÂÜÖÂÆπËá™Âä®ÂæÄ‰∏ãÊªö
}, 0)

